.parallax-page {
  height: auto;

  .parallax {
    position: static; }





  &__container {}

  &__body {
    padding: 50px 0 0 0;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 1;
    @include breakpoint(sm) {
      padding: 80px 0 0 0; }
    @include breakpoint(lg) {
      padding: 80px 0 0 0; } } }



